
import React from "react";
import ImgBca from "../assets/images/bca.png";
import ImgBni from "../assets/images/bni.png";
import Words from "../assets/images/words.svg";
import ImgPaypal from "../assets/images/paypal.png";
import Helpers from "../utils/Helpers.js";

interface Props {
	handleOnClick: () => void,
}

class Footer extends React.Component<Props, any, {}>{
	constructor(props: Props){
		super(props);	
		this.state = {
			currentYear: undefined,
		}
	}
	
	componentDidMount(){
		this.getCurrentYear();
	}
	
    getCurrentYear = (): void=>{
		const currentYear =  Helpers.getCurrentYear();
		this.setState({ currentYear : currentYear });
	}
		
	public render(): React.ReactNode {
		const { currentYear } = this.state;
		return(

			<React.Fragment>
				
				<section className="desc-footer text-black text-center text-start">	
					<div className="container">
						<div className="row">
							<div className="col-sm-12">								
								<div className="row">
									<div className="fluid py-3">
										<div className="container text-center">
											<div className="row">
												
												<div className="col-md-3 pb-2">
													<div className="card shadow border-0 bground-blue">	
														<div className="row">
															<label>	<p className="borderTopRight bg-warning close float-end mt-0 text-white p-2"> Payment Support </p>						   
															</label>
														</div>
														
														
														
														<ul className="list-unstyled pt-2 pb-3">
															<li><img className="ps-1" src={ImgBca} alt="BCA"/>
															<img className="ps-1" src={ImgBni} alt="BNI"/>
															<img className="ps-1" src={ImgPaypal} alt="PAYPAL"/></li>
																
														</ul>
														
													</div>
												</div>	

												
												<div className="col-md-6 pb-2">
													<div className="card shadow border-0 bground-blue">	
														<div className="row">
															<label>	<p className="borderTopRight bg-secondary close float-end mt-0  p-2"> <img src={Words} alt="words"/></p>						   
															</label>
														</div>															
														<ul className="list-unstyled">
															<li><label className="text-white"><h6>Fast Response : </h6></label></li>
															<li><label className="text-white"><h6><i className="fa fa-phone"></i> (+62) 852 7244 9998</h6></label></li>
															<li><label className="text-white"><i className="fa fa-calendar-check-o"></i> Monday - Saturday 8:30 Am - 17:00 Pm </label></li>
															<li><label className="text-white"><i className="fa fa-map-marker"></i> Address: Jl. Bhakti IV No.11 Rt.002/ Rw.011<br/> Arengka-Pekanbaru 28282</label></li>
															<li><label className="text-white"><i className="fa fa-envelope"></i> E-mail : info@cosinecode.com</label></li>
														</ul>																											
													</div>
												</div>	


												
												
												<div className="col-md-3 pb-2">
													<div className="card shadow border-0 bground-blue">	
														<div className="row">
															<label>	<p className="borderTopRight bg-warning close float-end mt-0 text-white p-2"> Our Services </p>						   
															
															</label>
															
														</div>
														
														<ul className="list-unstyled pt-2 pb-3">
															<li><label className="text-white">Web Development</label></li>
															<li><label className="text-white">Mobile Development</label></li>
															<li><label className="text-white">Desktop Development</label></li>
														</ul>														
													</div>
												</div>												
											</div>
										</div>	
									</div>
									
									<div className="container p-2 pb-0 text-center text-white">					              
										<section className="mb-0">	
											<i className="fa fa-instagram text-white p-2"></i>
											<i className="fa fa-twitter text-white p-2"></i>
										</section>	
									</div>
									
								</div>
							</div>							
						</div>
					</div>		
				</section>
				
				
				
					
				<footer className=" footer text-center p-3 text-white ">
					  {currentYear} | 
					<label className="text-white ps-1">cosinecode.com. All rights reserved</label>					
					<i className="bi bi-arrow-up-circle text-primary-blue fs-3 float-end d-flex" style={{cursor:"pointer"}} onClick={this.props.handleOnClick}></i>
						
				</footer>	  
            	
			</React.Fragment>
		)

	}
}

export default Footer;

