import React from "react";
import Brand from "../assets/images/brand.svg";

interface Props {
    toggleNavbar: () =>void,
	className: string,
	isExpanded?:boolean,
	home: string,
	services: string,
	portfolio: string,
	client: string,
	about: string,
	faq: string,
	contact : string,
	
}

class Header extends React.Component<Props, {}>
{
	public render(): React.ReactNode {
	 	return(
		
			<React.Fragment>
				<nav className={ this.props.className }>
					<div className="container">
                        <a className="navbar-brand" href="/"><img src={Brand} alt="brand" /></a>                            
						<button className="navbar-toggler collapsed" onClick = { this.props.toggleNavbar }
							type="button" data-bs-toggle="collapse"
							data-bs-target="#navbarResponsive"  
							aria-controls="navbarResponsive"
							aria-expanded="false" 
							aria-label="Toggle Navigation">
							<span className="fa fa-bars"></span>    
						</button>
					
							<div className={ this.props.isExpanded ? 'collapse navbar-collapse' : 'navbar-collapse'} id="navbarResponsive">
						  
								<ul className="navbar-nav ms-auto">  
							   
									<li className="nav-item"><a className="nav-link text-white" href="/">{this.props.home}</a></li>            
									<li className="nav-item"><a className="nav-link text-white" href="#services">{this.props.services}</a></li>
									<li className="nav-item"><a className="nav-link text-white" href="#portfolio">{this.props.portfolio}</a></li>
									<li className="nav-item"><a className="nav-link text-white" href="#our-clients">{this.props.client}</a></li>             
									<li className="nav-item"><a className="nav-link text-white" href="#about-us">{this.props.about}</a></li>       
									<li className="nav-item"><a className="nav-link text-white" href="#faq">{this.props.faq}</a></li>    								
									<li className="nav-item"><a className="nav-link text-white" href="#contact-us">{this.props.contact}</a></li>
									
								
								</ul>
							</div>

                    </div>
	            </nav>
		
			</React.Fragment>
		)
	}

}

export default Header;

