			isClicked: false,
			loadingLabel: 'Please wait !! Your Email is Sending ...',
			isAgreed: false,
			
			ip:'',
			latitude: '',
			longitude: '',
			city: '',
			region: '',
			country_name: '',
			agreed: 'yes',
			icon : "bi bi-arrow-down-circle float-end p-1 pb-1 text-muted",
