import { createClient } from "@supabase/supabase-js";
const supabaseURL: string      =  process.env.REACT_APP_DATABASE_URL as string ;
const supabaseAnonKey: string  =  process.env.REACT_APP_SUPABASE_SERVICE_API_KEY as string; 

const supabase = createClient(supabaseURL,supabaseAnonKey)
export default supabase;